

.modal {
   height: 100vh;
   width: 100vw;
   z-index: 5;
   background-color: rgba(0, 0, 0, 0.4);
   position: fixed;
   top: 0;
   left: 0;
   display: flex;
   align-items: center;
   justify-content: center;
   opacity: 0;
   pointer-events: none;
   transition: 0.5s;
}

.modal.active {
   opacity: 1;
   pointer-events: all;
}

.modal__content {
   display: flex;
   justify-content: center;
   padding: 20px;
   border-radius: 26px;
   background-color: white;
   transform: scale(0.5);
   transition: 0.4s all;
   width: 880px;
}

.modal__content.active {
   transform: scale(1);
}


@media (max-width: 1024px){
   .modal__content {
      width: 700px;
    }

}


@media (max-width: 767px) {
   .modal__content {
      width: 400px;
    } 
}
@media (max-width: 450px) {
   .modal__content {
      width: 270px;
      padding: 10px 20px;
    } 
}
